.hero{
  &__wrapper{
    height: 643px;
    display: grid;
    grid-template-columns: auto calc(760px + (1077 - 760) * ((100vw - 1440px) / (1920 - 1440)));
    &-block{
      height: 100%;
    }
  }
  &__video{
    background: #D9D9D9;
    width: 100%;
    height: 100%;
    min-height: 310px;
  }
  &__content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    &-description{
      margin: 10px 0 0 0;

      max-width: 490px;
    }
    &-footer{
      margin: 40px 0 0 0;

      max-width: 532px;
    }
  }

  &__title{
    font-size: 128px;
    line-height: 90%;

    color:$textColorBlack;
  }

  &__footer{
    display: grid;
    grid-template-columns: repeat(2,minmax(0,1fr));

    gap: 16px;
  }

  &__button{
    height: 43px;
  }
}


@media screen and (max-width: 1240px) {
  .hero{
    &__wrapper{
      height: auto;
      grid-template-columns: minmax(0,1fr);
      padding: 0 0 50px 0;
      &-block{
        &:nth-child(1){
          grid-row-start: 2;
        }
        &:nth-child(2){
          grid-row-start: 1;
        }
      }
    }
    &__content{
      padding: 20px 0 0 0;
    }
  }
}

@media screen and (max-width: 600px) {
  .hero{
    &__title{
      font-size: 61px;
    }
    &__content{
      &-description{
        margin: 20px 0 0 0;
      }
      &-footer{
        margin: 30px 0 0 0;
      }
    }
    &__footer{
      grid-template-columns: minmax(0,1fr);
      gap: 10px;
    }
  }
}