.section {
  &-rate {
    &__header {
      display: flex;
      align-items: center;
      justify-content: center;
    }

    &__title {
      text-align: center;
      font-family: 'Bebas Neue Cyrillic', sans-serif;
      font-weight: 400;
      font-size: 162px;
      line-height: 90%;

      color: $textColorBlack;

      max-width: 865px;
    }

    &__content {
      margin: 24px 0 0 0;

      display: flex;
      align-items: center;
      justify-content: center;
    }

    &__button {
      max-width: 320px;
      height: 50px;
    }
  }
}

@media screen and (max-width: 991px) {
  .section{
    &-rate{
      &__title{
        font-size: 63px;
        max-width: 330px;
      }
      &__wrapper{
        padding: 277px 0;
      }
    }
  }
}