/***
    The new CSS reset - version 1.7.3 (last updated 7.8.2022)
    GitHub page: https://github.com/elad2412/the-new-css-reset
***/
/*
    Remove all the styles of the "User-Agent-Stylesheet", except for the 'display' property
    - The "symbol *" part is to solve Firefox SVG sprite bug
 */
*:where(:not(html, iframe, canvas, img, svg, video, audio):not(svg *, symbol *)) {
  all: unset;
  display: revert;
}

/* Preferred box-sizing value */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Reapply the pointer cursor for anchor tags */
a, button {
  cursor: revert;
}

/* Remove list styles (bullets/numbers) */
ol, ul, menu {
  list-style: none;
}

/* For images to not be able to exceed their container */
img {
  max-width: 100%;
}

/* removes spacing between cells in tables */
table {
  border-collapse: collapse;
}

/* Safari - solving issue when using user-select:none on the <body> text input doesn't working */
input, textarea {
  -webkit-user-select: auto;
}

/* revert the 'white-space' property for textarea elements on Safari */
textarea {
  white-space: revert;
}

/* minimum style to allow to style meter element */
meter {
  -webkit-appearance: revert;
  appearance: revert;
}

/* reset default text opacity of input placeholder */
::placeholder {
  color: unset;
}

/* fix the feature of 'hidden' attribute.
   display:revert; revert to element instead of attribute */
:where([hidden]) {
  display: none;
}

/* revert for bug in Chromium browsers
   - fix for the content editable attribute will work properly.
   - webkit-user-select: auto; added for Safari in case of using user-select:none on wrapper element*/
:where([contenteditable]:not([contenteditable="false"])) {
  -moz-user-modify: read-write;
  -webkit-user-modify: read-write;
  overflow-wrap: break-word;
  -webkit-line-break: after-white-space;
  -webkit-user-select: auto;
}

/* apply back the draggable feature - exist only in Chromium and Safari */
:where([draggable="true"]) {
  -webkit-user-drag: element;
}

@font-face {
  font-family: 'Bebas Neue Cyrillic';
  src: url("../fonts/BebasNeueCyrillic/BebasNeueCyrillic.woff2") format("woff2"), url("../fonts/BebasNeueCyrillic/BebasNeueCyrillic.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Inter';
  src: url("../fonts/Inter/Inter-Regular/Inter-Regular.woff2") format("woff2"), url("../fonts/Inter/Inter-Regular/Inter-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Inter';
  src: url("../fonts/Inter/Inter-SemiBold/Inter-SemiBold.woff2") format("woff2"), url("../fonts/Inter/Inter-SemiBold/Inter-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

body {
  color: #212121;
}

.container {
  padding: 0 100px;
}

.title {
  font-family: 'Bebas Neue Cyrillic', sans-serif;
  font-weight: 400;
}

.button {
  -webkit-appearance: none;
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 40px;
}

.button-green {
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: #FFFFFF;
  background: #3F9D83;
  border-radius: 30px;
}

.button-white {
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #3F9D83;
  border: 1px solid #3F9D83;
  border-radius: 30px;
}

.button-black {
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #FFFFFF;
  background: #3E3E3E;
  border-radius: 40px;
}

.button-arrow {
  display: flex;
  align-items: center;
  width: max-content;
}

.button-arrow:hover .button-arrow__icon::before {
  transform: scale(1);
}

.button-arrow:hover svg path {
  fill: #fff;
}

.button-arrow:hover .button-arrow__text {
  color: #fff;
  background-color: #3E3E3E;
}

.button-arrow:hover .button-arrow__text-green {
  background-color: #3F9D83;
}

.button-arrow__text {
  height: 47px;
  width: 255px;
  border-radius: 30px;
  border: 1px solid #3E3E3E;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #3E3E3E;
  transition: 0.3s color cubic-bezier(0.215, 0.61, 0.355, 1), 0.3s background-color cubic-bezier(0.215, 0.61, 0.355, 1);
}

.button-arrow__text-green {
  color: #3F9D83;
  border-color: #3F9D83;
}

.button-arrow__icon {
  width: 47px;
  height: 47px;
  border-radius: 50%;
  border: 1px solid #3E3E3E;
  flex-shrink: 0;
  margin: 0 0 0 10px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.button-arrow__icon svg {
  position: relative;
  z-index: 2;
}

.button-arrow__icon svg path {
  fill: #3E3E3E;
  transition: 0.3s fill cubic-bezier(0.215, 0.61, 0.355, 1);
}

.button-arrow__icon::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #3E3E3E;
  border-radius: 50%;
  transform: scale(0);
  transition: 0.3s transform cubic-bezier(0.215, 0.61, 0.355, 1);
}

.button-arrow__icon-green {
  border-color: #3F9D83;
}

.button-arrow__icon-green svg path {
  fill: #3F9D83;
}

.button-arrow__icon-green::before {
  background-color: #3F9D83;
}

.icon {
  width: 100%;
  height: 100%;
}

.advantages-scrollbar.swiper-scrollbar, .tariffs-scrollbar.swiper-scrollbar, .statistics-scrollbar.swiper-scrollbar {
  position: relative;
  background: transparent;
  height: 3px;
  border-bottom: 1px solid #C7C7C7;
}

.advantages-scrollbar.swiper-scrollbar .swiper-scrollbar-drag, .tariffs-scrollbar.swiper-scrollbar .swiper-scrollbar-drag, .statistics-scrollbar.swiper-scrollbar .swiper-scrollbar-drag {
  height: 3px;
  background: #fff;
}

.content {
  padding: 70px 0 0 0;
}

@media screen and (max-width: 1600px) {
  .container {
    padding: 0 20px;
  }
}

@media screen and (max-width: 1100px) {
  .content {
    padding: 80px 0 0 0;
  }
}

@media screen and (max-width: 600px) {
  .container {
    padding: 0 16px;
  }
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 9;
  background-color: #fff;
  transition: .5s transform linear;
}

.header.hidden {
  transform: translateY(-100%);
}

.header__info {
  display: flex;
  align-items: center;
}

.header__info-menu {
  padding: 0 15px 0 0;
  position: relative;
}

.header__info-menu::after {
  content: '';
  position: absolute;
  right: 0;
  top: -14px;
  width: 1px;
  height: 70px;
  background-color: #E3EBF6;
}

.header__info-logo {
  padding: 0 0 0 30px;
}

.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 70px;
}

.header__wrapper-block_small {
  max-width: 210px;
  width: 100%;
}

.header__wrapper-block_burger {
  display: none;
}

.header__button {
  width: 178px;
}

.header-logo__wrapper {
  display: block;
  width: 95px;
  height: 45px;
}

.header-logo__img {
  width: 100%;
  height: 100%;
}

.header-nav__list {
  display: flex;
  align-items: center;
}

.header-nav__item + .header-nav__item {
  margin: 0 0 0 50px;
}

.header-nav__link {
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: #212121;
  position: relative;
  display: block;
}

.header-nav__link::after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  bottom: -.15em;
  height: .05em;
  background: #212121;
  will-change: transform;
  transform-origin: 100% 100%;
  transform: scaleX(0);
  pointer-events: none;
  transition-property: transform;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  transition-duration: .5s;
}

.header-nav__link-wrapper {
  position: relative;
  display: block;
  overflow: hidden;
  height: 22px;
}

.header-nav__link-block {
  display: block;
  position: relative;
  transition-property: transform;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  transition-duration: .5s;
}

.header-nav__link-hover {
  position: absolute;
  left: 0;
  top: 0;
  transform: translateY(125%);
}

.header-nav__link:hover .header-nav__link-default {
  transform: translateY(-125%);
}

.header-nav__link:hover .header-nav__link-hover {
  transform: translateY(0);
}

.header-nav__link:hover::after {
  transform-origin: 0 0;
  transform: scaleX(1);
}

.header-menu__icon {
  width: 40px;
  height: 40px;
}

.header-menu__icon:hover .light-green {
  fill: #3F9D83;
}

.header-menu__icon:hover .deep-green {
  fill: #48B798;
}

.header-menu__icon svg {
  width: 100%;
  height: 100%;
}

.header-menu__icon svg rect {
  transition: .3s fill linear;
}

.burger {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  flex-direction: column;
  width: 30px;
  height: 17px;
}

.burger__item {
  flex-shrink: 0;
  width: 100%;
  height: 2px;
  background-color: #1B1B1B;
  border-radius: 30px;
}

.burger__item:nth-child(2) {
  width: 25px;
}

.burger__item:nth-child(3) {
  width: 20px;
}

@media screen and (max-width: 1100px) {
  .header__info-menu {
    display: none;
  }
  .header__info-logo {
    padding: 0;
  }
  .header-logo__wrapper {
    width: 60px;
    height: 25px;
  }
  .header__wrapper {
    height: 80px;
    align-items: flex-end;
    padding: 20px 0;
  }
  .header__wrapper-block_burger {
    display: block;
  }
  .header__wrapper-block_desktop {
    display: none;
  }
}

.footer__wrapper {
  background: #1B1B1B;
  padding: 100px 0 30px 0;
}

.footer__row + .footer__row {
  margin: 75px 0 0 0;
}

.footer__title-text {
  font-family: 'Bebas Neue Cyrillic', sans-serif;
  font-weight: 400;
  font-size: 96px;
  line-height: 90%;
  color: #FFFFFF;
}

.footer__title-text + .footer__title-text {
  margin: 5px 0 0 0;
}

.footer__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer__list-text {
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 90%;
  color: #FFFFFF;
}

.footer__list-link {
  position: relative;
  display: block;
  width: max-content;
}

.footer__list-link:hover::before {
  animation: link .75s forwards;
}

.footer__list-link::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: -3px;
  height: 1px;
  background: #fff;
  will-change: transform;
  transform-origin: 0 0;
  transform: scaleX(1);
  -webkit-animation: none;
  animation: none;
  pointer-events: none;
  transition-property: transform;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  transition-duration: .5s;
}

@media screen and (max-width: 1600px) {
  .footer__list {
    flex-wrap: wrap;
  }
  .footer__list-item {
    flex: 33%;
    flex-shrink: 0;
    height: 50px;
    display: flex;
    align-items: center;
  }
  .footer__list-item:nth-child(1) {
    order: 4;
  }
  .footer__list-item:nth-child(4) {
    order: 1;
  }
  .footer__list-item:nth-child(3) {
    order: 3;
    justify-content: flex-end;
  }
  .footer__list-item:nth-child(2) {
    order: 2;
    justify-content: center;
  }
  .footer__list-item:nth-child(5) {
    order: 5;
    justify-content: center;
  }
  .footer__list-item:nth-child(6) {
    order: 6;
    justify-content: flex-end;
  }
}

@media screen and (max-width: 991px) {
  .footer__wrapper {
    padding: 50px 0;
  }
  .footer__title-text {
    font-size: 48px;
  }
  .footer__title-text + .footer__title-text {
    margin: 0;
  }
  .footer__row + .footer__row {
    margin: 30px 0 0 0;
  }
  .footer__list-item {
    flex: 100%;
  }
  .footer__list-item:nth-child(odd), .footer__list-item:nth-child(even) {
    justify-content: flex-start;
  }
  .footer__list-item:nth-child(1) {
    order: 7;
  }
}

.section__wrapper {
  padding: 100px 0;
}

.section__wrapper_bordered {
  border-radius: 50px 50px 0px 0px;
}

.section__wrapper_black {
  background: #1B1B1B;
}

.section__wrapper_purple {
  background: #6244B0;
}

.section__wrapper_green {
  background: #3F9D83;
}

.section__title {
  text-transform: uppercase;
  color: #fff;
  font-size: 96px;
  line-height: 90%;
}

.section__description {
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;
  color: #212121;
}

.section__description_white {
  color: #fff;
}

@media screen and (max-width: 600px) {
  .section__wrapper {
    padding: 50px 0;
  }
  .section__title {
    font-size: 48px;
  }
  .section__description {
    font-size: 16px;
    line-height: 22.4px;
  }
}

.hero__wrapper {
  height: 643px;
  display: grid;
  grid-template-columns: auto calc(760px + (1077 - 760) * ((100vw - 1440px) / (1920 - 1440)));
}

.hero__wrapper-block {
  height: 100%;
}

.hero__video {
  background: #D9D9D9;
  width: 100%;
  height: 100%;
  min-height: 310px;
}

.hero__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}

.hero__content-description {
  margin: 10px 0 0 0;
  max-width: 490px;
}

.hero__content-footer {
  margin: 40px 0 0 0;
  max-width: 532px;
}

.hero__title {
  font-size: 128px;
  line-height: 90%;
  color: #212121;
}

.hero__footer {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 16px;
}

.hero__button {
  height: 43px;
}

@media screen and (max-width: 1240px) {
  .hero__wrapper {
    height: auto;
    grid-template-columns: minmax(0, 1fr);
    padding: 0 0 50px 0;
  }
  .hero__wrapper-block:nth-child(1) {
    grid-row-start: 2;
  }
  .hero__wrapper-block:nth-child(2) {
    grid-row-start: 1;
  }
  .hero__content {
    padding: 20px 0 0 0;
  }
}

@media screen and (max-width: 600px) {
  .hero__title {
    font-size: 61px;
  }
  .hero__content-description {
    margin: 20px 0 0 0;
  }
  .hero__content-footer {
    margin: 30px 0 0 0;
  }
  .hero__footer {
    grid-template-columns: minmax(0, 1fr);
    gap: 10px;
  }
}

