    min-width: 1500px;
  }
}

@media screen and (max-width: 600px) {
  .advantages__grid {
    grid-template-columns: repeat(4, minmax(0, calc(100vw - 32px)));
    min-width: auto;
    width: max-content;
  }
  .advantages__grid-item {
    border: none;
  }
  .advantages-item__header + .advantages-item__content {
    margin: 50px 0 0 0;
  }
  .advantages-item__number {
    font-size: 100px;
    line-height: 90%;
  }
  .advantages-item__title {
    font-size: 36px;
    line-height: 90%;
  }
  .advantages-item__wrapper {
    padding: 5px 0 0 0;
  }
}

.section-how__description {
  margin: 50px 0 0 0;
  max-width: 600px;
}

.section-how__content {
  margin: 70px 0 0 0;
}

.how-item__wrapper {
  display: flex;
  align-items: flex-start;
  padding: 3px 0 0 0;
}

.how-item__number {
  font-family: 'Bebas Neue Cyrillic', sans-serif;
  font-size: 176px;
  line-height: 90%;
  color: transparent;
  -webkit-text-stroke-width: 3px;
  -webkit-text-stroke-color: #FFFFFF;
}

.how-item__content {
  margin: 0 0 0 20px;
}

.how-item__text {
  font-family: 'Inter', sans-serif;
  font-weight: 600;
  font-size: 20px;
  line-height: 125%;
  color: #FFFFFF;
}

@media screen and (max-width: 1600px) {
  .how-item__number {
    font-size: 143px;
  }
  .how-item__content {
    margin: 0 0 0 16px;
  }
  .how-item__text {
    font-size: 16px;
  }
}

@media screen and (max-width: 1100px) {
