    content: '';
    position: absolute;
    z-index: 2;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100px;
    display: block;
    background: linear-gradient(0deg, #6244B0 20%, rgba(99, 69, 176, 0) 100%);
    transition: .3s opacity;
  }
  .how-button-prev {
    content: '';
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
