  }
}

.section-setup__content {
  margin: 72px 0 0 0;
}

.setup__grid {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  grid-auto-rows: 253px;
  gap: 45px;
}

.setup-item {
  height: 100%;
  width: 100%;
}

.setup-item:hover .setup-item__wrapper {
  background-color: #FFFFFF;
}

.setup-item:hover .setup-item__title {
  color: #1B1B1B;
}

.setup-item:hover .setup-item__icon svg .black-fill {
  fill: #1B1B1B;
}

.setup-item:hover .setup-item__icon svg .black-stroke {
  stroke: #1B1B1B;
}

.setup-item__wrapper {
  width: 100%;
  height: 100%;
  border: 1px solid rgba(255, 255, 255, 0.59);
  border-radius: 15px;
  padding: 40px;
  display: flex;
  flex-direction: column;
  transition: .3s background-color;
}

.setup-item__title {
  font-family: 'Bebas Neue Cyrillic', sans-serif;
  font-weight: 400;
  font-size: 58px;
  line-height: 90%;
  color: #FFFFFF;
  transition: .3s color;
}

