.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: -1;
  transition: .3s opacity;
}

.popup.active {
  opacity: 1;
  z-index: 10;
}

.popup-trigger:hover, .popup-close:hover {
  cursor: pointer;
}

.popup-wrapper {
  width: 100%;
  height: 100%;
}

.popup-menu {
  background: #1B1B1B;
}

.popup-menu__content {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  height: 100%;
  padding: 10vw 0 20px 0;
  overflow-x: hidden;
  overflow-y: auto;
}

.popup-menu__close {
  width: 40px;
  height: 40px;
  position: absolute;
  top: 60px;
  right: 120px;
  background: url("../img/close.svg") center center/contain no-repeat;
}

.menu-list__item + .menu-list__item {
  margin: 72px 0 0 0;
}

.menu-list__link {
  font-family: 'Bebas Neue Cyrillic', sans-serif;
  font-weight: 400;
  font-size: 85px;
  line-height: 90%;
  color: #FFFFFF;
  display: flex;
  align-items: flex-end;
  position: relative;
  width: max-content;
}

.menu-list__link::after {
  content: '';
  width: 60px;
  height: 60px;
  display: block;
  flex-shrink: 0;
  background: url("../img/arrow-link.svg") center center/contain no-repeat;
  margin: 0 0 0 35px;
}

.menu-list__link:hover .menu-list__link-text::before {
  animation: link .75s forwards;
}

.menu-list__link-text {
  display: block;
  width: max-content;
  position: relative;
}

.menu-list__link-text::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 6px;
  height: 4px;
  background: #fff;
  will-change: transform;
  transform-origin: 0 0;
  transform: scaleX(1);
  -webkit-animation: none;
  animation: none;
  pointer-events: none;
  transition-property: transform;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  transition-duration: .5s;
}

@media screen and (max-width: 1440px) {
  .popup-menu__close {
    right: 20px;
    top: 20px;
  }
}

@keyframes link {
  0%, 0.1% {
    transform-origin: 100% 100%;
    transform: scaleX(1);
  }
  49.9% {
    transform-origin: 100% 100%;
    transform: scaleX(0);
  }
  50%, 50.1% {
    transform-origin: 0 0;
    transform: scaleX(0);
  }
  to {
    transform-origin: 0 0;
    transform: scaleX(1);
  }
}
