.footer{
  &__wrapper{
    background: $blackColor;

    padding: 100px 0 30px 0;
  }
  &__row{
    & + &{
      margin: 75px 0 0 0;
    }
  }
  &__title{
    &-text{
      font-family: 'Bebas Neue Cyrillic', sans-serif;
      font-weight: 400;
      font-size: 96px;
      line-height: 90%;

      color: #FFFFFF;
      & + &{
        margin: 5px 0 0 0;
      }
    }
  }
  &__list{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-text{
      font-family: 'Inter', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 90%;

      color: #FFFFFF;
    }
    &-link{
      position: relative;
      display: block;
      width: max-content;
      &:hover::before{
        animation: link .75s forwards;
      }
      &::before{
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        bottom: -3px;
        height: 1px;
        background: #fff;
        will-change: transform;
        transform-origin: 0 0;
        transform: scaleX(1);
        -webkit-animation: none;
        animation: none;
        pointer-events: none;
        transition-property: transform;
        transition-timing-function: cubic-bezier(.215,.61,.355,1);
        transition-duration: .5s;
      }
    }
  }
}

@media screen and (max-width: 1600px) {
  .footer{
    &__list{
      flex-wrap: wrap;
      &-item{
        flex: 33%;
        flex-shrink: 0;
        height: 50px;
        display: flex;
        align-items: center;
        &:nth-child(1){
          order: 4;
        }
        &:nth-child(4){
          order: 1;
        }
        &:nth-child(3){
          order: 3;
          justify-content: flex-end;
        }
        &:nth-child(2){
          order: 2;
          justify-content: center;
        }
        &:nth-child(5){
          order: 5;
          justify-content: center;

        }
        &:nth-child(6){
          order: 6;
          justify-content: flex-end;
        }
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .footer{
    &__wrapper{
      padding: 50px 0;
    }
    &__title{
      &-text{
        font-size: 48px;
        & + &{
          margin: 0;
        }
      }
    }
    &__row{
      & + &{
        margin: 30px 0 0 0;
      }
    }
    &__list{
      &-item{
        flex: 100%;
        &:nth-child(odd),&:nth-child(even){
          justify-content: flex-start;
        }
        &:nth-child(1){
          order: 7;
        }
      }
    }
  }
}

