.popup{
  position: fixed;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  opacity: 0;
  z-index: -1;

  transition: .3s opacity;
  &.active{
    opacity: 1;
    z-index: 10;
  }
  &-trigger,&-close{
    &:hover{
      cursor: pointer;
    }
  }
  &-wrapper{
    width: 100%;
    height: 100%;
  }
  &-menu{
    background: $blackColor;
    &__content{
      display: flex;
      align-items: flex-start;
      justify-content: center;

      height: 100%;

      padding: 10vw 0 20px 0;

      overflow-x: hidden;
      overflow-y: auto;
    }
    &__close{
      width: 40px;
      height: 40px;

      position: absolute;
      top: 60px;
      right: 120px;

      background: url("../img/close.svg") center center/contain no-repeat;
    }
  }
}

.menu-list{
  &__item{
    & + &{
      margin: 72px 0 0 0;
    }
  }
  &__link{
    font-family: 'Bebas Neue Cyrillic', sans-serif;
    font-weight: 400;
    font-size: 85px;
    line-height: 90%;

    color: #FFFFFF;
    display: flex;
    align-items: flex-end;

    position: relative;

    width: max-content;
    &::after{
      content: '';
      width: 60px;
      height: 60px;
      display: block;
      flex-shrink: 0;

      background: url("../img/arrow-link.svg") center center/contain no-repeat;

      margin: 0 0 0 35px;
    }

    &:hover{
      .menu-list__link-text{
        &::before{
          animation: link .75s forwards;
        }
      }
    }
    &-text{
      display: block;
      width: max-content;
      position: relative;
      &::before{
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        bottom: 6px;
        height: 4px;
        background: #fff;
        will-change: transform;
        transform-origin: 0 0;
        transform: scaleX(1);
        -webkit-animation: none;
        animation: none;
        pointer-events: none;
        transition-property: transform;
        transition-timing-function: cubic-bezier(.215,.61,.355,1);
        transition-duration: .5s;
      }
    }
  }
}

@media screen and (max-width: 1440px) {
  .popup{
    &-menu{
      &__close{
        right: 20px;
        top: 20px;
      }
    }
  }
}