body {
  color: $textColorBlack;
}

.container {
  padding: 0 100px;
}

.title {
  font-family: 'Bebas Neue Cyrillic', sans-serif;
  font-weight: 400;
}

.button {
  -webkit-appearance: none;
  border: none;
  outline: none;

  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: 40px;

  &-green {
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    font-size: 14px;

    color: #FFFFFF;

    background: $greenColor;
    border-radius: 30px;
  }

  &-white {
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    font-size: 16px;

    color: $greenColor;
    border: 1px solid $greenColor;
    border-radius: 30px;
  }

  &-black {
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    font-size: 16px;

    color: #FFFFFF;
    background: #3E3E3E;
    border-radius: 40px;
  }

  &-arrow {
    display: flex;
    align-items: center;
    width: max-content;

    &:hover &__icon::before{
      transform: scale(1);
    }
    &:hover svg path{
      fill: #fff;
    }
    &:hover &__text{
      color:#fff;
      background-color: #3E3E3E;
      &-green{
        background-color: $greenColor;
      }
    }
    &__text {
      height: 47px;
      width: 255px;

      border-radius: 30px;

      border: 1px solid #3E3E3E;

      display: flex;
      align-items: center;
      justify-content: center;

      font-family: 'Inter', sans-serif;
      font-weight: 400;
      font-size: 16px;

      color: #3E3E3E;

      transition: .3s color cubic-bezier(.215,.61,.355,1), .3s background-color cubic-bezier(.215,.61,.355,1);

      &-green{
        color: $greenColor;
        border-color: $greenColor;
      }
    }
    &__icon {
      width: 47px;
      height: 47px;

      border-radius: 50%;
      border: 1px solid #3E3E3E;
      flex-shrink: 0;

      margin: 0 0 0 10px;

      position: relative;

      display: flex;
      align-items: center;
      justify-content: center;
      svg{
        position: relative;
        z-index: 2;
        path{
          fill:#3E3E3E;

          transition: .3s fill cubic-bezier(.215,.61,.355,1);
        }
      }

      &::before{
        content: '';
        position: absolute;
        left: 0;
        top: 0;

        width: 100%;
        height: 100%;

        background-color: #3E3E3E;

        border-radius: 50%;

        transform: scale(0);

        transition: .3s transform cubic-bezier(.215,.61,.355,1);
      }

      &-green {
        border-color: $greenColor;

        svg{
          path{
            fill:$greenColor;
          }
        }

        &::before{
          background-color: $greenColor;
        }
      }
    }
  }
}

.icon {
  width: 100%;
  height: 100%;
}

.advantages, .tariffs, .statistics {
  &-scrollbar {
    &.swiper-scrollbar {
      position: relative;
      background: transparent;
      height: 3px;

      border-bottom: 1px solid #C7C7C7;

      .swiper-scrollbar {
        &-drag {
          height: 3px;
          background: #fff;
        }
      }
    }
  }
}

.content {
  padding: 70px 0 0 0;
}

@media screen and (max-width: 1600px) {
  .container {
    padding: 0 20px;
  }
}

@media screen and (max-width: 1100px) {
  .content {
    padding: 80px 0 0 0;
  }
}

@media screen and (max-width: 600px) {
  .container {
    padding: 0 16px;
  }
}