.section {
  &-how {
    &__description {
      margin: 50px 0 0 0;
      max-width: 600px;
    }

    &__content {
      margin: 70px 0 0 0;
    }
  }
}

.how {
  &__grid {
  }

  &-item {
    &__wrapper {
      display: flex;
      align-items: flex-start;
      padding: 3px 0 0 0;
    }

    &__number {
      font-family: 'Bebas Neue Cyrillic', sans-serif;
      font-size: 176px;
      line-height: 90%;

      color: transparent;

      -webkit-text-stroke-width: 3px;
      -webkit-text-stroke-color: #FFFFFF;
    }

    &__content {
      margin: 0 0 0 20px;
    }

    &__text {
      font-family: 'Inter', sans-serif;
      font-weight: 600;
      font-size: 20px;
      line-height: 125%;

      color: #FFFFFF;
    }
  }
}

@media screen and (max-width: 1600px) {
  .how {
    &-item {
      &__number {
        font-size: 143px;
      }

      &__content {
        margin: 0 0 0 16px;
      }

      &__text {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 1100px) {
  .how {
    position: relative;
    &-button{
      &-next{
        content: '';
        position: absolute;
        z-index: 2;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100px;
        display: block;

        background: linear-gradient(0deg, $purpleColor 20%, rgba(#6345B0, 0) 100%);

        transition: .3s opacity;
      }
      &-prev{
        content: '';
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 50px;
        display: block;

        background: linear-gradient(180deg, $purpleColor 0%, rgba(#6345B0, 0) 100%);
        transition: .3s opacity;
      }
      &-next,&-prev{
        &.swiper-button-disabled{
          opacity: 0;
        }
      }
    }
    &::before {

    }

    &::after {

    }

    &__grid {
      height: 400px;
      grid-template-columns: minmax(0, 1fr);
      gap: 0;
    }
  }
}

@media screen and (max-width: 600px) {
  .section {
    &-how {
      &__description {
        margin: 20px 0 0 0;
      }
    }
  }
  .how {
    &-item {
      &__number {
        font-size: 110px;
      }

      &__content {
        margin: 0 0 0 13px;
      }

      &__text {
        font-size: 14px;
      }
    }
  }
}