.section{
  &-statistics{
    &__content{
      margin: 72px 0 0 0;
    }
    &__footer{
      margin: 46px 0 0 0;
      display: none;
    }
  }
}

.statistics{
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,minmax(0,1fr));
    grid-auto-rows: 485px;
    width: 100%;
    &-item{
      &:last-child{
        .statistics-item__content{
          width: 485px;
          position: absolute;
          right: 0;
        }
        .statistics-item__circle{
          right: 0;
          left: unset;
        }
      }
      &:first-child{
        .statistics-item__content{
          width: 485px;
          left: 0;
          position: absolute;
        }
        .statistics-item__circle{
          left: 0;
        }
      }
    }
  }
  &-item{
    max-width: 485px;
    height: 100%;
    &__wrapper{
      position: relative;

      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      height: 100%;
      width: 100%;
    }
    &__circle{
      position: absolute;
      left: calc(50% - 242.5px);
      top: 0;

      border-radius: 50%;
      border: 1px solid #fff;

      svg{
        circle{
          r:48.5px!important;
        }
      }
    }
    &__content{
      &-footer{
        margin: 40px 0 0 0;

        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
    &__number{
      font-family: 'Bebas Neue Cyrillic', sans-serif;
      font-weight: 400;
      font-size: 70px;
      line-height: 90%;

      color: #FFFFFF;

      text-align: center;
    }
    &__text{
      font-family: 'Inter', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 90%;

      text-align: center;

      color: #FFFFFF;

      max-width: 210px;
    }
  }
}

@media screen and (max-width: 1700px) {
 .statistics{
   &__grid{
     grid-auto-rows: 400px;
     &:last-child{
       .statistics-item__content{
         width: 400px;
       }
     }
     &:first-child{
       .statistics-item__content{
         width: 400px;
       }
     }
   }
   &-item{
     &__circle{
       left: calc(50% - 200px);
       width: 400px!important;
       height: 400px!important;
     }
     svg{
       width: 400px!important;
       height: 400px!important;
     }
   }
 }
}

@media screen and (max-width: 1240px) {
  .statistics{
    &__grid{
      grid-template-columns: repeat(4,minmax(0,400px));
      width: max-content;
      gap: 15px;
    }
  }

  .section{
    &-statistics{
      &__footer{
        display: block;
      }
      &__content{
        margin: 30px 0 0 0;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .statistics{
    &__grid{
      grid-template-columns: repeat(4,minmax(0,calc(100vw - 32px)));
      grid-auto-rows: calc(100vw - 32px);

      &-item{
        &:last-child{
          .statistics-item__content{
            position: relative;
            width: 100%;
            transform: translateX(0);
          }
        }
        &:first-child{
          .statistics-item__content{
            position: relative;
            width: 100%;
            transform: translateX(0);
          }
        }
      }
    }
    &-item{
      svg{
        width: calc(100vw - 32px) !important;
        height: calc(100vw - 32px) !important;
      }

      &__circle{
        left: 0;
        top: 0;
        width: calc(100vw - 32px) !important;
        height: calc(100vw - 32px) !important;
      }

      &__content{
        &-footer{
          margin: 30px 0 0 0;
        }
      }

      &__number{
        font-size: 50px;
      }
      &__text{
        max-width: 270px;
      }
    }
  }
}