.header{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 9;

  background-color: #fff;

  transition: .5s transform linear;
  &.hidden{
    transform: translateY(-100%);
  }
  &__info{
    display: flex;
    align-items: center;
    &-menu{
      padding: 0 15px 0 0;
      position: relative;
      &::after{
        content: '';
        position: absolute;
        right: 0;
        top: -14px;
        width: 1px;
        height: 70px;

        background-color: #E3EBF6;
      }
    }
    &-logo{
      padding: 0 0 0 30px;
    }
  }
  &__wrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;

    height: 70px;
    &-block{
      &_small{
        max-width: 210px;
        width: 100%;
      }
      &_burger{
        display: none;
      }
    }
  }

  &__button{
    width: 178px;
  }

  &-logo{
    &__wrapper{
      display: block;
      width: 95px;
      height: 45px;
    }
    &__img{
      width: 100%;
      height: 100%;
    }
  }

  &-nav{
    &__list{
      display: flex;
      align-items: center;
    }
    &__item{
      & + &{
        margin: 0 0 0 50px;
      }
    }
    &__link{
      font-family: 'Inter', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 140%;

      color:$textColorBlack;

      position: relative;
      display: block;

      &::after{
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        bottom: -.15em;
        height: .05em;
        background: $textColorBlack;
        will-change: transform;
        transform-origin: 100% 100%;
        transform: scaleX(0);
        pointer-events: none;
        transition-property: transform;
        transition-timing-function: cubic-bezier(.215,.61,.355,1);
        transition-duration: .5s;
      }

      &-wrapper{
        position: relative;
        display: block;
        overflow: hidden;
        height: 22px;
      }

      &-block{
        display: block;
        position: relative;
        transition-property: transform;
        transition-timing-function: cubic-bezier(.215,.61,.355,1);
        transition-duration: .5s;
      }

      &-hover{
        position: absolute;
        left: 0;
        top: 0;
        transform: translateY(125%);
      }

      &:hover &-default{
        transform: translateY(-125%);
      }
      &:hover &-hover{
        transform: translateY(0);
      }
      &:hover::after{
        transform-origin: 0 0;
        transform: scaleX(1);
      }
    }
  }

  &-menu{
    &__icon{
      width: 40px;
      height: 40px;
      &:hover{
        .light-green{
          fill:#3F9D83;
        }
        .deep-green{
          fill: #48B798;
        }
      }
      svg{
        width: 100%;
        height: 100%;
        rect{
          transition: .3s fill linear;
        }
      }
    }
  }
}

.burger{
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  flex-direction: column;

  width: 30px;
  height: 17px;

  &__item{
    flex-shrink: 0;

    width: 100%;
    height: 2px;

    background-color: $blackColor;
    border-radius: 30px;
    &:nth-child(2){
      width: 25px;
    }
    &:nth-child(3){
      width: 20px;
    }
  }
}

@media screen and (max-width: 1100px) {
  .header{
    &__info{
      &-menu{
        display: none;
      }
      &-logo{
        padding: 0;
      }
    }
    &-logo{
      &__wrapper{
        width: 60px;
        height: 25px;
      }
    }
    &__wrapper{
      height: 80px;

      align-items: flex-end;

      padding: 20px 0;
      &-block{
        &_burger{
          display: block;
        }
        &_desktop{
          display: none;
        }
      }
    }
  }
}