.section{
  &-setup{
    &__content{
      margin: 72px 0 0 0;
    }
  }
}

.setup{
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,minmax(0,1fr));
    grid-auto-rows: 253px;
    gap: 45px;
  }
  &-item{
    height: 100%;
    width: 100%;

    &:hover &__wrapper{
      background-color: #FFFFFF;
    }
    &:hover &__title{
      color:$blackColor;
    }
    &:hover &__icon{
      svg{
        .black-fill{
          fill:$blackColor;
        }
        .black-stroke{
          stroke: $blackColor;
        }
      }
    }

    &__wrapper{
      width: 100%;
      height: 100%;

      border: 1px solid rgba(255, 255, 255, 0.59);
      border-radius: 15px;

      padding: 40px;

      display: flex;
      flex-direction: column;

      transition: .3s background-color;
    }
    &__title{
      font-family: 'Bebas Neue Cyrillic', sans-serif;
      font-weight: 400;
      font-size: 58px;
      line-height: 90%;

      color: #FFFFFF;

      transition: .3s color;
    }
    &__content{
      margin: auto 0 0 auto;
    }
    &__icon{
      svg{
        width: 36px;
        height: 36px;
        path{
          transition: .3s fill, .3s stroke;
        }
        circle{
          transition: .3s stroke;
        }
      }
    }
  }
}

@media screen and (max-height: 1600px) {
  .setup{
    &__grid{
      gap: 40px;
    }
  }
}

@media screen and (max-width: 1100px) {
  .setup{
    &__grid{
      grid-template-columns: minmax(0,1fr);
      gap: 20px;
    }
    &-item{
      &__wrapper{
        padding: 30px;
      }
      &__title{
        font-size: 30px;
      }
      &__icon{
        svg{
          width: 30px;
          height: 30px;
        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .section{
    &-setup{
      &__content{
        margin: 30px 0 0 0;
      }
    }
  }
  .setup{
    &__grid{
      grid-auto-rows: 168px;
    }
  }
}