.section {
  &__wrapper {
    padding: 100px 0;

    &_bordered{
      border-radius: 50px 50px 0px 0px;
    }
    &_black{
      background: $blackColor;
    }
    &_purple{
      background: $purpleColor;
    }
    &_green{
      background: $greenColor;
    }
  }

  &__title{
    text-transform: uppercase;
    color:#fff;
    font-size: 96px;
    line-height: 90%;
  }

  &__description {
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    font-size: 20px;
    line-height: 140%;

    color: $textColorBlack;

    &_white{
      color:#fff;
    }
  }
}

@media screen and (max-width: 600px) {
  .section{
    &__wrapper{
      padding: 50px 0;
    }
    &__title{
      font-size: 48px;
    }
    &__description{
      font-size: 16px;
      line-height: 22.4px;
    }
  }
}