.section{
  &-tariffs{
    &__info{
      display: flex;
      flex-direction: column;
      height: 100%;
      &-description{
        margin: 20px 0 0 0;
      }
      &-footer{
        margin: auto 0 0 0;
      }
      &-button{
        height: 50px;
        max-width: 312px;
      }
    }
    &__header{
      display: none;
    }
    &__button{
      margin: 30px 0 0 0;

      height: 55px;

      display: none;
    }
    &__description{
      font-family: 'Inter', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: #FFFFFF;

      max-width: 480px;
    }
  }
}


.tariffs{
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,minmax(0,1fr));
    grid-auto-rows: 385px;
    gap: 40px;
  }
  &-item{
    height: 100%;
    &__wrapper{
      padding: 20px;

      background: #FFFFFF;
      border-radius: 15px;

      display: flex;
      flex-direction: column;

      height: 100%;
    }
    &__title{
      font-family: 'Bebas Neue Cyrillic', sans-serif;
      font-weight: 400;
      font-size: 96px;
      line-height: 90%;

      letter-spacing: 0.02em;

      color: transparent;

      -webkit-text-stroke-width: 2px;
      -webkit-text-stroke-color: $greenColor;
    }
    &__content{
      margin: 20px 0 0 0;
    }
    &__list{
      max-width: 350px;
      &-item{
        & + &{
          margin: 12px 0 0 0;
        }

        display: flex;
        align-items: center;
        &::before{
          content: '';
          width: 24px;
          height: 24px;
          display: block;

          flex-shrink: 0;

          margin: 0 6px 0 0;

          background-repeat: no-repeat;
          background-position: center center;
          background-size: contain;
        }
        &_shop{
          &::before{
            background-image: url("../img/shop.svg");
          }
        }
        &_profile{
          &::before{
            background-image: url("../img/profile.svg");
          }
        }
        &_box{
          &::before{
            background-image: url("../img/box.svg");
          }
        }
        &_settings{
          &::before{
            background-image: url("../img/settings.svg");
          }
        }
      }
      &-text{
        font-family: 'Inter', sans-serif;
        font-weight: 500;
        font-size: 16px;
        line-height: 100%;
        letter-spacing: -0.03em;

        color: $textColorBlack;
      }
    }
    &__footer{
      margin: auto 0 0 0;
    }
    &__price{
      font-family: 'Bebas Neue Cyrillic', sans-serif;
      font-weight: 400;
      font-size: 64px;
      line-height: 90%;

      color: #3E3E3E;
    }
  }
}


@media screen and (max-width: 1600px) {
  .section{
    &-tariffs{
      &__info{
        &-description{
          display: none;
        }
      }
    }
  }
  .tariffs{
    &__grid{
      grid-auto-rows: 345px;
    }
  }
}

@media screen and (max-width: 1400px) {
  .tariffs{
    &__grid{
      grid-template-columns: repeat(2,minmax(0,1fr));
      grid-auto-rows: 320px;
    }
  }
}

@media screen and (max-width: 991px) {
  .section{
    &-tariffs{
      &__header{
        display: block;
      }
      &__info{
        display: none;
      }
      &__content{
        margin: 30px 0 0 0;
      }
      &__footer{
        margin: 40px 0 0 0;
      }
      &__button{
        display: flex;
      }
    }
  }
  .tariffs{
    &__grid{
      grid-template-columns: repeat(5,minmax(0,calc(100vw - 40px)));
      width: max-content;
    }
  }
}

@media screen and (max-width: 600px) {
  .tariffs{
    &-item{
      &__title{
        letter-spacing: 0.06em;
        font-size: 45px;
      }
      &__price{
        font-size: 45px;

      }
    }
  }
}